.post {
  padding: 25px;
  margin: 25px 0;
  border: 1px dashed white;
}

.posts-container {
  width: 80vw;
  max-width: 800px;
}

.filter-container {
  width: 80vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  max-width: 800px;
}

input {
  width: 80vw;
  max-width: 800px;
  padding: 10px;
  border-radius: 5px;
  padding: 12px;
  font-size: 16px;
}

h1 {
  margin: 25px 100px;
  text-align: center;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

@media (max-width: 590px) {
  h1 {
    font-size: 1.6rem;
    margin: 25px 0;
  }
}

